@import url(https://fonts.googleapis.com/css?family=Baloo+Thambi+2&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.bg-tagline {
    position: relative;
    background: url(/catering-deployed-github/static/media/bgLanding.ff76a4c0.jpg);
    background-size:cover;
    min-height: 100vh;
    background-position: center;
}

.tagline {
    background: rgba(255, 255, 255, 0.3)
}
.zoom-image-health {
    overflow: hidden
}

.zoom-image-health img {
    transition: all 0.5s ease
}

.zoom-image-health:hover img {
    transform: scale(1.2);
}


@media only screen and (max-width: 768px) {
    .card-box-health {
        display: flex;
        flex-wrap: nowrap!important;
        overflow-x: scroll!important;
        overflow-y: hidden!important;
    }
    .card-mobile-health {
        flex: 0 0 auto;
    }
    h1 {
        font-size: 40px!important;
    }
}


.zoom-image-mainmenu{
    overflow: hidden;
}

.zoom-image-mainmenu img {
    transition: all 0.5s ease
}

.zoom-image-mainmenu:hover img {
    transform: scale(1.2);
}

.card-image-mainmenu {
    width: 100%;
    height: 20vw;
    object-fit: cover;
    position: relative;
}

.discount-mainmenu{
    background-color: red;
    width: 20%;
    text-align: center;
    color: white;
    font-weight: bold;
    position: absolute;
    right: 0;
    top: 20px;
}

.nama-paket-mainmenu {
    font-weight: bold;
}

.discounted-price-mainmenu {
    color: grey;
    text-decoration: line-through

}

.discount-price-mainmenu {
    color:red;
}

.card-box-mainmenu {
    display: flex;
    flex-wrap: nowrap!important;
    overflow-x: scroll!important;
    overflow-y: hidden!important;
    align-content: center;
    align-items: center;
    scroll-behavior: smooth;
}

.scrolled-card-mainmenu {
    flex: 0 0 auto;
}

.button-scroll {
    background-color: rgba(192, 192, 192, 0.5);
    border: none;
    padding-top: 0%;
    padding-bottom: 0.5%;
    text-align: left;
    text-decoration: none;
    display: inline-block;
    font-size: 2vw;
    cursor: pointer;
    border-radius: 50%;
    position: absolute
}

.button-scroll:focus {
    outline: none;
}

.button-scroll2 {
    background-color: rgba(192, 192, 192, 0.884);
    border: none;
    color:black;
    padding-top: 0%;
    padding-bottom: 0.5%;
    text-align: left;
    text-decoration: none;
    display: inline-block;
    font-size: 2vw;
    cursor: pointer;
    border-radius: 50%;
    position: absolute;
    right: 0
}

.button-scroll2:focus{
    outline: none;
}

@media only screen and (max-width: 768px) {
    .card-image-mainmenu {
        height: 200px!important;
    }

    .font-price-mainmenu {
        font-size: 13px;
    }

    .nama-paket-mainmenu {
        font-size: 13px!important;
    }

    .discount-price-mainmenu {
        font-size: 13px!important;
    }

    .discounted-price-mainmenu {
        font-size: 13px!important;
    }
    
    .button-scroll {
        font-size: 20px;
    }

    .button-scroll2 {
        font-size: 20px;
    }
}

.card-box-mainmenu::-webkit-scrollbar {
    display: none;
}

